import Testimonials from './components/Testimonials'
import Services from './components/Services'

import Footer from './components/Footer'


class App extends Component {
  render() {
    return (
      <div>
        <Navigation />
        <h1>About Me</h1>
        <About />
        <h1>Session Information</h1>
        <Services />
        <h2>Book A Session</h2>
        <Schedule />
        <h1>Fanfare</h1>
        <Testimonials />
        <Footer />

    </div>
    );
  }
}

export default App;



