import React from 'react';
import { Navbar, Nav, NavItem } from 'react-bootstrap'
import SocialButtons from './SocialButtons'

import './Navigation.css'

const Navigtaion = () => {
  return (
    <Navbar className="Navbar" collapseOnSelec>
      <Navbar.Header>
        <Navbar.Brand>
          <h1>Alfonso Tech / Coding Bootcamp Mentor</h1>
        </Navbar.Brand>
        <Navbar.Toggle />
      </Navbar.Header>
      <Navbar.Collapse>
        <Nav pullRight>

          <NavItem eventKey={1}>
            <SocialButtons />
          </NavItem>
          {/* <NavItem eventKey={2}>

          </NavItem> */}


        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

export default Navigtaion



