import React from 'react'
import './Work.css'
import workways from "../workways.json";

const Work = () => {
  return (
      <div className="Work">
          {/* <h2 className="work-header">Ways Of Working Together</h2> */}
        {workways.map( (work, index) => {
          console.log('work.img', work.img)
          return (
            <div index={index}>

              <h4>{work.name}</h4>
              <div className="work-div">
                <img className="work-images" src={work.img} alt={work.name}/>
                <p className="work-text">{work.description}</p>
              </div>

            </div>
          )
        })}
      </div>
  )
}

export default Work



