import React from 'react';
import { Image, Col } from 'react-bootstrap'
import Work from './Work'
import './About.css'






const TechStack = () => {

  const urls = []
  for (var i =0; i<12;i++) {
    urls.push('./assets/images/tech-stack-imgs/download-'+i+'.png')
  }

    return (
      <div className="TechStack">
          <p>I specialize in teaching <strong>Full-Stack JavaScript</strong> development and the <strong>MERN stack</strong> (MongoDB, Express, React, and Node). I regularly teach:</p>

        <ul className="technologies">
          {urls.map( (url, index) => {

            return <li className="logo-item" key={index}>
              <img className="logo-image" src={url} alt="tech-logo"/>
            </li>

          })}
      </ul>
    </div>
  )


}




const About = () => {
  return (

      <div className="About">
        <div className='body'>
          <div className='logo'>
              <Col xs={1} md={4}>
            <Image src="./assets/images/logo-me.jpg" circle />
              </Col>
          </div>

          <div className='about-description'>
            <p>Hi, I'm Rita Alfonso, and I'm all about <strong>#EdTech</strong> and <strong>#TechEd</strong>.</p>
            <p>I have 10+ years experience in higher education and I've been on instructional teams at <strong>Code Fellows</strong>, <strong>U.C. Berkeley</strong>, and <strong>OpenClassrooms</strong>. I've helped more than a hundred students to become web developers, and I can help you too!</p>
            {/* <p>I specialize in teaching <strong>Full-Stack JavaScript</strong> development and the <strong>MERN stack</strong> (MongoDB, Express, React, and Node). I regularly teach:</p> */}

          </div>
        </div>
        <TechStack />

        <h2 className="work-header">Ways Of Working Together</h2>
        <Work className="clear" />
      </div>

  )
}

export default About



